.user-story-text-container {
  
  &__content {
    animation: opacityAnimation 0.8s ease;

    &__name {
      text-transform: uppercase;
      font-weight: bold;
      letter-spacing: 0.12rem;
      margin-bottom: 0.6rem;
    }
    
    &__date {
      display: block;
      margin-bottom: 0.6rem;
      font-weight: 600;
      line-height: 2rem;
      letter-spacing: 0.2rem;
    }

    &__description {
      font-size: 1.3rem;
      line-height: 2rem;
    }

    &:first-child {
      margin-top: 2rem;
    }

    &:not(:last-of-type) {
      margin-bottom: 3.2rem;
    }
  }
}
